body {
  margin: 10px 0;
}

#main {
  position: relative;
  height: 375px;
  width: 375px;
}

.cutting-line {
  display: inline-block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  height: 100%;
  width: 100%;
}
.cutting-line::before {
  display: inline-block;
  margin-top: 50%;
  border-top: 1px dashed #FF0000;
  height: 0;
  width: 50%;
  content: "";
}
